@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Raleway&family=Titillium+Web&display=swap');

h1, h2, h3, p {
  /*font-family: 'Montserrat', sans-serif;*/
  font-family: 'Titillium Web', sans-serif;
}

.header {
  position: sticky;
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: #7BE3FE;
  align-items: center;
}

#content_anchor {
  height: 50px;
}

.title {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 36px;
}

.intro {
  margin: auto;
  padding: 0 0 0 0;
  width: 50vw;
  text-align: center;
  font-size: 36px;
  margin-bottom: 40px;
}

.welcome {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: chartreuse;
  align-items: center;
  justify-content: center;
}

.welcome h1 {
  font-size: 48px;
  margin: 0 0 0 0;
}

.animation {
  background: url('./Images/animated_code.gif') no-repeat fixed;
  background-size: cover;
}

.intro p {
  font-size: 24px;
  margin-top: 20px;
  margin-bottom: 40px;
}

.skills {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-content: flex-start;
  flex-wrap: wrap;
  text-align: center;
}

.single_skill {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 10vh;
  width: 33%;
}

.skill_image {
  align-self: center;
}

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.single_project {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
}

.single_project p {
  font-size: 18px;
  text-align: center;
}

.line {
  background-color: #7BE3FE;
  width: 75%;
  height: 5px;
  margin-bottom: 15px;
}

.project_image {
  width: 100%;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%
}

.hide {
  display: none;
}

.carousel-control-next-icon,
.carousel-control-prev-icon {
  filter: invert(1);
}

.section_line {
  background-color: #7BE3FE;
  width: 75%;
  height: 5px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #7BE3FE;
  height: 100px;
}

.footer div {
  margin-left: 8vw;
  margin-right: 8vw;
}

.footer p {
  margin-bottom: .3rem;
}

button {
  margin-top: 20px !important;
  background-color: #7BE3FE;
  height: 6vh;
  width: 15vw;
}